<template>
        <form @submit.prevent="submitForm">
            <input type="text" v-model="cidadeInput" placeholder="Digite o nome da cidade">
            <button><i class='bx bx-search' ></i></button>
        </form>
</template>


<script>
    export default {
        name: 'Form',

        props: {
            cidade: String,
        },

        data() {
            return {
                cidadeInput: this.cidade
            }
        },  

        methods: {
            submitForm() {
                this.$emit('getData', this.cidadeInput)
                console.log(this.cidadeInput)
            }
        },
    }
</script>


<style scoped>
    form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }

    input {
        background-color: rgba(220,220,220,.3);
        padding: 10px 20px;
        font-size: 1.3rem;
        border-radius: 50px;
        outline: none;
        color: #fafafa;
        width: 100%;
        position: relative;
        transition: .2s;
    }

    input:focus {
        filter: brightness(0.95);
    }

    input::placeholder {
        color: rgba(255,255,255,.4);
    }

    button {
        background-color: transparent;
        cursor: pointer;
    }

    button:hover {
        animation: btn infinite linear .9s
    }

    @keyframes btn {
        0% {transform: translateY(0)}
        25% {transform: translateY(3px)}
        50% {transform: translateY(5px)}
        75% {transform: translateY(3px)}
        100% {transform: translateY(0)}
    }

    i {
        color: rgba(255,255,255,.5);
        font-size: 2.7rem;
    }
</style>