<template>
  <div class="main-container">
    <Form :cidade="cidade" @getData="getData" />
    <Result 
    :cidade="cidade"
    :graus="graus"
    :clima="clima"
    :umidade="umidade"
    :imgUrl="imgUrl"
    />
  </div>
</template>


<script>
  import Form from './components/Form.vue';
  import Result from './components/Result.vue';

  export default {
    name: 'App',

    data() {
      return {
        cidade: '',
        graus: '',
        clima: '',
        imgUrl: '',
        umidade: 0,
        apiKey: '50f547014fa9a072c742f3b188b2c806',
      }
    },

    components: {
      Form,
      Result
    },

    methods: {
      async getData(cidade) {
        const req = await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${cidade}&appid=${this.apiKey}&lang=pt_br&units=metric`)
        const data = await req.json()
        this.cidade = data.name
        this.fillResult(data)
        console.log(data)
        console.log(this.cidade)
      },

      fillResult(data) {
        this.graus = data.main.temp.toFixed(1)
        this.clima = data.weather[0].description
        this.umidade = data.main.humidity
        switch (data.weather[0].description) {
          case 'céu limpo':
            this.imgUrl = require('@/assets/img/sun.png')
            break
          case 'nublado' || 'algumas nuvens':
            this.imgUrl = require('@/assets/img/cloud.png')
            break
          case 'nuvens dispersas':
            this.imgUrl = require('@/assets/img/solnuvem.png')
            break

          case 'chuviscos com intensidade de raios':
            this.imgUrl = require('@/assets/img/storm.png')
            break

          case 'garoa de leve intensidade':
            this.imgUrl = require('@/assets/img/chuvaleve.png')
            break
        }
        console.log(data.weather[0].description)
      }
    }
  }
</script>


